import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import item from '@/views/item'
import msg from '@/views/msg'
import month from '@/views/month'
import general from '@/views/general'
import time from '@/views/time'
import date from '@/views/date'
import custom from '@/views/custom'
import custommsg from '@/views/custommsg'
import govenment from '@/views/govenment'
import govenmsg from '@/views/govenmsg'
import partner from '@/views/partner'
import partnermsg from '@/views/partnermsg'
import competitor from '@/views/competitor'
import compemsg from '@/views/compemsg'
import keypro from '@/views/keypro'
import keypromsg from '@/views/keypromsg'
import busdata from '@/views/busdata'
import busmsg from '@/views/busmsg'
import tab from '@/views/tab'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/Home',
      name: 'Home',
      component: Home
    }, {
      path: '/',
      redirect: '/Home'
    },{
      path: '/item',
      name: 'item',
      component: item
    },{
      path: '/msg',
      name: 'msg',
      component: msg
    },{
      path: '/month',
      name: 'month',
      component: month
    },{
      path: '/general',
      name: 'general',
      component: general
    },{
      path: '/time',
      name: 'time',
      component: time
    },{
      path: '/date',
      name: 'date',
      component: date
    },{
      path: '/custom',
      name: 'custom',
      component: custom
    },{
      path: '/custommsg',
      name: 'custommsg',
      component: custommsg
    },{
      path: '/govenment',
      name: 'govenment',
      component: govenment
    },{
      path: '/govenmsg',
      name: 'govenmsg',
      component: govenmsg
    },{
      path: '/partner',
      name: 'partner',
      component: partner
    },{
      path: '/partnermsg',
      name: 'partnermsg',
      component: partnermsg
    },{
      path: '/competitor',
      name: 'competitor',
      component: competitor
    },{
      path: '/compemsg',
      name: 'compemsg',
      component: compemsg
    },{
      path: '/keypro',
      name: 'keypro',
      component: keypro
    },{
      path: '/keypromsg',
      name: 'keypromsg',
      component: keypromsg
    },{
      path: '/busdata',
      name: 'busdata',
      component: busdata
    },{
      path: '/busmsg',
      name: 'busmsg',
      component: busmsg
    },{
      path: '/tab',
      name: 'tab',
      component: tab
    }
  ]
})



