<template>
    <div class="keypro">
        <el-row>
        <el-col :span="24" class="zai-title"><div class="grid-content bg-purple-dark">
            <el-col :span="8"><div class="grid-content zai-title-title bg-purple">经营数据上报</div></el-col>
        </div></el-col>
        </el-row>
        <el-row class="indexof">
            <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="省份：">
                        <el-select v-model="form.province" style="width:60%">
                        <el-option 
                        v-for="i in form.provinces"
                        :key="i.value"
                        :label="i.label"
                        :value="i.value"
                        ></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="类别：">
                        <el-select v-model="form.item" style="width:60%">
                        <el-option 
                        v-for="i in form.items"
                        :key="i.value"
                        :label="i.label"
                        :value="i.value"
                        ></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
        </el-row>
        <el-row>
        <el-col :span="24"><div class="grid-content tables bg-purple-dark">
            <el-table
                :data="tableData"
                border
                style="width: 100%"
                :header-cell-style="{background:'#E6E6E6',color:'#000'}"
                :cell-style="first">
                <el-table-column
                fixed
                label="操作"
                align="center"
                width="50">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small" style="outline:none;">查看</el-button>
                </template>
                </el-table-column>
                <el-table-column
                fixed
                prop="date"
                label=""
                align="center"
                width="50">
                </el-table-column>
                <el-table-column
                prop="state"
                label="状态"
                align="center"
                width="60">
                </el-table-column>
                <el-table-column
                prop="keyitem"
                label="类别"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="province"
                label="省份"
                align="center"
                width="60">
                </el-table-column>
                <el-table-column
                prop="proname"
                label="项目名称"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="reptime"
                label="报告时间"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="reportime"
                label="上报时间"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="remarks"
                label="备注"
                align="center"
                min-width="100">
                </el-table-column>
            </el-table>
        </div></el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form:{
                province:'上海',
                provinces:[{
                    value:'上海',
                    label:'上海'
                },{
                    value:'北京',
                    label:'北京'
                },{
                    value:'天津',
                    label:'天津'
                }],
                item:'',
                items:[{
                    value:'年度',
                    label:'年度'
                },{
                    value:'月度',
                    label:'月度'
                }],
            },
            tableData: [{
                date: '1',
                state:'',
                keyitem:'年度',
                province:'北京',
                proname:'',
                reptime:'2018-02-18',
                reportime:'',
                remarks:''
                }, {
                date: '2',
                state:'',
                keyitem:'月度',
                province:'上海',
                proname:'',
                reptime:'2018-07',
                reportime:'',
                remarks:''
                },{
                date: '3',
                state:'',
                keyitem:'',
                province:'天津',
                proname:'',
                reptime:'',
                reportime:'',
                remarks:''
                }]  
        }
    },
    methods:{
        search(){
            console.log(1)
        },
        first({row,column,rowIndex,columnIndex}){
            // console.log(row,column,rowIndex,columnIndex)
            if((rowIndex===1||rowIndex===2||rowIndex===3||row.date)&&(columnIndex===0||columnIndex===1)){
                return "background:#E6E6E6"
            }
        },
        handleClick(row) {
            this.$router.push({path:'/busmsg',query:{busmsg:row}})
        }
    }
}
</script>
<style lang="scss">
.keypro{
    .zai-title{
        background: #9AD5F8;
        .zai-title-title{
            background: #fff;
            margin-left: 5px;
            text-align: center;
            font-size: 12px;
            line-height: 26px;
        }
    }
    .indexof{
        background: #E6F3FF;
        padding-bottom: 8px;
        .el-form-item{
            margin-bottom: 0 !important;
        }
        .el-form-item__label{
            font-size: 10px !important;
            margin-bottom: 0 !important;
        }
        .el-col{
            margin-top: 8px;
        }
    }
    .tables{
        .el-table td,.el-table th{
            padding:6px 0 !important;
        }
        .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed{
            border-right: 1px solid #ccc !important;
        }
        .el-table td, .el-table th.is-leaf{
            border-bottom: 1px solid #ccc;
        }
        .el-button--small, .el-button--small.is-round {
            padding:0 0;
        }
    }
}
</style>



