<template>
    <div class="zaijian">
        <el-row>
        <el-col :span="24" class="zai-title"><div class="grid-content bg-purple-dark">
            <el-col :span="10"><div class="grid-content zai-title-title bg-purple">在建项目管理</div></el-col>
        </div></el-col>
        </el-row>
        <el-row class="indexof">
            <el-col :xs="24" :sm="12" :md="4"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="省份">
                        <el-select v-model="form.region" placeholder="-请选择-" style="width:60%">
                        <el-option label="上海" value="上海"></el-option>
                        <el-option label="北京" value="北京"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple-light">
                <el-form :model="form" label-width="80px" size="mini">
                    <el-form-item label="项目名称">
                        <el-input v-model="form.user" style="width:60%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="4"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="项目状态">
                        <el-select v-model="form.building" style="width:60%">
                        <el-option 
                        v-for="i in form.build"
                        :key="i.value"
                        :label="i.label"
                        :value="i.value"
                        ></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="工程类别">
                        <el-select v-model="form.items" style="width:60%">
                        <el-option 
                        v-for="i in form.item"
                        :key="i.value"
                        :label="i.label"
                        :value="i.value"
                        ></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="4"><div class="grid-content bg-purple-light">
                <el-form label-width="80px" size="mini">
                    <el-form-item>
                        <el-button style="background:#F2F2F2;border:1px solid #CCCFD2;outline:none" @click="search">搜索</el-button>
                    </el-form-item>
                </el-form>
            </div></el-col>
        </el-row>
        <el-row>
        <el-col :span="24"><div class="grid-content tables bg-purple-dark">
            <el-table
                :data="tableData"
                border
                style="width: 100%"
                :header-cell-style="{background:'#E6E6E6',color:'#000'}">
                <el-table-column
                fixed
                label="操作"
                align="center"
                width="50">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small" style="outline:none;">查看</el-button>
                </template>
                </el-table-column>
                <el-table-column
                fixed
                prop="date"
                label="序号"
                align="center"
                width="50">
                </el-table-column>
                <el-table-column
                prop="name"
                label="项目名称"
                align="center"
                width="120">
                </el-table-column>
                <el-table-column
                prop="work"
                label="业务板块"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="project"
                label="工程类别"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="item"
                label="项目模式"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="province"
                label="省份"
                align="center"
                width="70">
                </el-table-column>
                <el-table-column
                prop="data1"
                label="中标日期"
                align="center"
                width="100">
                </el-table-column>
                <el-table-column
                prop="money"
                label="合同金额"
                align="center"
                width="80">
                </el-table-column>
                <el-table-column
                prop="unit"
                label="施工单位"
                align="center"
                width="80">
                </el-table-column>
                <el-table-column
                prop="manager"
                label="项目经理"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="tel"
                label="联系电话"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="state"
                label="项目状态"
                align="center"
                width="100">
                </el-table-column>
                <el-table-column
                prop="win"
                label="是否获奖"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="punish"
                label="是否处罚"
                align="center"
                min-width="90">
                </el-table-column>
            </el-table>
        </div></el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form:{
                user:'',
                region: [{
                    value: '上海',
                    label: '上海'
                },{
                    value: '北京',
                    label: '北京'
                }],
                building:'在建',
                build:[{
                    value:'在建',
                    label:'在建'
                },{
                    value:'未建',
                    label:'未建'
                }],
                items:'轨道交通',
                item:[{
                    value:'轨道交通',
                    label:'轨道交通'
                },{
                    value:'民用建筑',
                    label:'民用建筑'
                }]
            },
            tableData: [{
                date: '1',
                name: '来源于中标项目',
                project: '轨道交通',
                work: '工程承包',
                item: '公开招标',
                province: '上海',
                data1:'2016-05-03',
                money:'20000',
                unit:'',
                manager:'',
                tel:'18601933060',
                state:'在建',
                win:'是',
                punish:'否'
                }, {
                date: '2',
                name: '十五局',
                project: '民用建筑',
                work: '工程承包',
                item: '私营企业',
                province: '上海',
                data1:'2016-06-05',
                money:'10020',
                unit:'',
                manager:'',
                tel:'18601933060',
                state:'在建',
                win:'是',
                punish:'否'
                }, {
                date: '3',
                name: '十四局',
                project: '隧道',
                work: '工程承包',
                item: '私营企业',
                province: '北京',
                data1:'2017-06-03',
                money:'20000',
                unit:'',
                manager:'',
                tel:'18134214803',
                state:'未建',
                win:'否',
                punish:'是'
                }]  
        }
    },
    methods:{
        search(){
            console.log(1)
        },
        handleClick(row) {
            this.$router.push({path:'/date',query:{date:row}})
        }
    }
}
</script>
<style lang="scss">
.zaijian{
    .zai-title{
        background: #9AD5F8;
        .zai-title-title{
            background: #fff;
            margin-left: 5px;
            text-align: center;
            font-size: 12px;
            line-height: 26px;
        }
    }
    .indexof{
        background: #E6F3FF;
        padding-bottom: 8px;
        .el-form-item{
            margin-bottom: 0 !important;
        }
        .el-form-item__label{
            font-size: 10px !important;
            margin-bottom: 0 !important;
        }
        .el-col{
            margin-top: 8px;
        }
    }
    .tables{
        .el-table td,.el-table th{
            padding:6px 0 !important;
        }
        .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed{
            border-right: 1px solid #ccc !important;
        }
        .el-table td, .el-table th.is-leaf{
            border-bottom: 1px solid #ccc;
        }
        .el-button--small, .el-button--small.is-round {
            padding:0 0;
        }
    }
}
</style>



