<template>
    <div>
        <!-- <div class="title" style="text-align:center">营业收入完成情况</div> -->
        <div id="myChart1" :style="{width:'100%',height:'300px',marginTop:'30px'}"></div>
    </div>
</template>
<script>
import $ from 'jquery'
export default {
    data(){
        return {
        //    ms:this.months,
        //    js:this.jindus,
        //    fs:this.finishs
        }
    },
    // props:['months','finishs','jindus'],
    mounted(){
        this.drawLine(); 
        // console.log(this.months)
    },
    methods:{
        drawLine(){
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById('myChart1'))
            // 绘制图表
            myChart.setOption({
                title:{
                    text:'营业收入完成情况',
                    left:'center',
                    textStyle:{
                        fontSize:16,
                        fontWeight:'normal'
                    }
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer : { // 坐标轴指示器，坐标轴触发有效
                        type : 'shadow' // 默认为直线，可选为：'line' | 'shadow'
                    }
                },
                grid:{
                    top:'20%',
                    bottom:'20%',
                    left:'10%',
                    right:'10%',
                    // height: '250',
                },
                legend: [
                    {
                        bottom: 0,
                        // right: "24%",
                        textStyle: {
                            color: "black",
                            fontSize: 12,
                            fontFamily: "微软雅黑"
                        },
                        itemWidth: 27,
                        itemHeight: 10,
                        data:[{
                            name: "完成（亿元）",
                            icon: "rect"
                        },{
                            name: "进度",
                            icon: "line"
                        }]
                    }
                ],
                xAxis: [
                        {
                            type: 'category',
                            data:["一月","二月","三月","四月","五月","六月","七月","八月","九月","十月","十一月","十二月"],
                            axisTick:false,
                            axisLabel: {
				                interval:0,//横轴信息全部显示
				                rotate:30,//-30度角倾斜显示    
                            }
                        }
                    ],
                    yAxis: [
                        {
                            axisLine: {show:false},
                            axisTick: {show:false},
                            // max:20,
                            type: 'value',
                            // name: '水量/ml',
                            min: 0,
                            max: 400,
                            interval: 200,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        },
                        {
                            axisLine: {show:false},//y轴线
                            axisTick: {show:false},//y刻度线
                            max:20,
                            type: 'value',
                            // name: '水量/ml',
                            min: 0,
                            max: 200,
                            interval: 100,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        }
                    ],
                series: [
                        {
                            name:'完成（亿元）',
                            type:'bar',
                            yAxisIndex:0,
                            barWidth: '15',
                            itemStyle: {
                                normal: {
                                    color: '#5B9BD5',
                                    /*信息显示方式*/
                                    // label: {
                                    //     show: true,
                                    //     position: 'top',
                                    //     // formatter: '{b}\n{c}'
                                    // }
                                }
                            },
                            data:["220","260","0","190","0","0","200","180","210","130","140","205"]
                        },
                        {
                            name:'进度',
                            type:'line',
                            yAxisIndex:1,
                            showSymbol:false,//拐点不显示圆圈
                            symbol:'none',//悬停不显示圆点
                            itemStyle : {  /*设置折线颜色*/
                                normal : {
                                    color:'#ED833C'
                                }
                            },
                            data:["10","30","25","50","60","40","40","100","80","120","110","90"]
                        }

                    ]
            });
            window.addEventListener("resize", () => { myChart.resize();});
        }
    }
}
</script>
<style scoped>
</style>



