<template>
    <div class="govenment">
        <el-row>
        <el-col :span="24" class="zai-title"><div class="grid-content bg-purple-dark">
            <el-col :span="8"><div class="grid-content zai-title-title bg-purple">政府资源</div></el-col>
        </div></el-col>
        </el-row>
        <el-row class="indexof">
            <el-col :xs="2" :sm="2" :md="2"><div class="grid-content bg-purple-light">
                &nbsp;
            </div></el-col>
            <el-col :xs="12" :sm="12" :md="8"><div class="grid-content bg-purple-light">
                <el-input v-model="form.user" placeholder="请输入客户名称查询" style="width:100%" size="mini"></el-input>
            </div></el-col>
            <el-col :xs="2" :sm="2" :md="2"><div class="grid-content bg-purple-light">
                &nbsp;
            </div></el-col>
            <el-col :xs="4" :sm="4" :md="4"><div class="grid-content bg-purple-light">
                <el-button style="background:#F2F2F2;border:1px solid #CCCFD2;outline:none" @click="search" size="mini">搜索</el-button>
            </div></el-col>
        </el-row>
        <el-row>
        <el-col :span="24"><div class="grid-content tables bg-purple-dark">
            <el-table
                :data="tableData"
                border
                style="width: 100%"
                :header-cell-style="{background:'#E6E6E6',color:'#000'}">
                <el-table-column
                fixed
                label="操作"
                align="center"
                width="50">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small" style="outline:none;">查看</el-button>
                </template>
                </el-table-column>
                <el-table-column
                fixed
                prop="date"
                label="序号"
                align="center"
                width="50">
                </el-table-column>
                <el-table-column
                prop="unitname"
                label="单位名称"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="province"
                label="省份"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="local"
                label="地市"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="person"
                label="负责人"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="tel"
                label="联系电话"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="remarks"
                label="备注"
                align="center"
                min-width="100">
                </el-table-column>
            </el-table>
        </div></el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form:{
                user:''
            },
            tableData: [{
                date: '1',
                unitname: 'A',
                province: '轨道交通',
                local: '工程承包',
                person: '公开招标',
                tel: '13365725692',
                remarks:'在建'
                }, {
                date: '2',
                unitname: 'A',
                province: '轨道交通',
                local: '工程承包',
                person: '公开招标',
                tel: '13365725692',
                remarks:'在建'
                },{
                date: '3',
                unitname: 'A',
                province: '轨道交通',
                local: '工程承包',
                person: '公开招标',
                tel: '13365725692',
                remarks:'在建'
                }]  
        }
    },
    methods:{
        search(){
            console.log(1)
        },
        handleClick(row) {
            this.$router.push({path:'/govenmsg',query:{govenmsg:row}})
        }
    }
}
</script>
<style lang="scss">
.govenment{
    .zai-title{
        background: #9AD5F8;
        .zai-title-title{
            background: #fff;
            margin-left: 5px;
            text-align: center;
            line-height: 26px;
            font-size: 12px;
        }
    }
    .indexof{
        background: #E6F3FF;
        padding-bottom: 8px;
        .el-form-item{
            margin-bottom: 0 !important;
        }
        .el-form-item__label{
            font-size: 10px !important;
            margin-bottom: 0 !important;
        }
        .el-col{
            margin-top: 8px;
        }
    }
    .tables{
        .el-table td,.el-table th{
            padding:6px 0 !important;
        }
        .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed{
            border-right: 1px solid #ccc !important;
        }
        .el-table td, .el-table th.is-leaf{
            border-bottom: 1px solid #ccc;
        }
        .el-button--small, .el-button--small.is-round {
            padding:0 0;
        }
    }
}
</style>



