<template>
    <div class="item">
        <el-row>
        <el-col :span="24"><div class="grid-content admin bg-purple-dark">
            <el-col :span="8"><div class="grid-content adminer bg-purple">中标项目管理</div></el-col>
        </div></el-col>
        </el-row>
        <el-row class="indexof">
        <el-col :xs="24" :sm="12" :md="5"><div class="grid-content bg-purple">
            <el-form :model="formInline" label-width="80px" size="mini">
                <el-form-item label="项目名称">
                    <el-input v-model="formInline.user" style="width:50%"></el-input>
                </el-form-item>
            </el-form>
        </div></el-col>
        <el-col :xs="24" :sm="12" :md="5"><div class="grid-content bg-purple-light">
            <el-form :model="formInline" label-width="80px" size="mini">
                <el-form-item label="省份">
                    <el-select v-model="value" style="width:50%">
                    <el-option
                    v-for="item in formInline.region"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
        </div></el-col>
        <el-col :xs="24" :sm="12" :md="5"><div class="grid-content bg-purple"></div>
            <el-form :model="formInline" label-width="80px" size="mini">
                <el-form-item label="开始日期">
                    <el-date-picker
                    v-model="formInline.date1"
                    type="date"
                    style="width:80%"
                    placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
            </el-form>
        </el-col>
        <el-col :xs="24" :sm="12" :md="5"><div class="grid-content bg-purple">
            <el-form :model="formInline" label-width="80px" size="mini">
                <el-form-item label="结束日期">
                   <el-date-picker
                    v-model="formInline.date2"
                    type="date"
                    style="width:80%"
                    placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
            </el-form>
        </div></el-col>
        <el-col :xs="24" :sm="12" :md="4"><div class="grid-content bg-purple-light">
            <el-form :model="formInline" label-width="80px" size="mini">
                <el-form-item>
                    <el-button style="background:#F2F2F2;border:1px solid #CCCFD2;outline:none" @click="search">搜索</el-button>
                </el-form-item>
            </el-form>
        </div></el-col>
        </el-row>
        <el-row>
        <el-col :span="24"><div class="grid-content bg-purple-dark">
            <el-table
                :data="tableData"
                border
                style="width: 100%"
                :header-cell-style="{background:'#E6E6E6',color:'#000'}"
                :cell-style="first">
                <el-table-column
                fixed
                label="操作"
                align="center"
                width="50">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small" style="outline:none;">查看</el-button>
                </template>
                </el-table-column>
                <el-table-column
                fixed
                prop="date"
                align="center"
                width="50">
                </el-table-column>
                <el-table-column
                prop="name"
                label="项目名称"
                align="center"
                width="80">
                </el-table-column>
                <el-table-column
                prop="work"
                label="业务板块"
                align="center"
                width="80">
                </el-table-column>
                <el-table-column
                prop="project"
                label="工程类别"
                align="center"
                width="80">
                </el-table-column>
                <el-table-column
                prop="item"
                label="项目模式"
                align="center"
                width="80">
                </el-table-column>
                <el-table-column
                prop="province"
                label="省份"
                align="center"
                width="60">
                </el-table-column>
                <el-table-column
                prop="win"
                label="中标单位名称"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="data1"
                label="中标日期"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="data2"
                label="合同签署日期"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="money"
                label="合同金额"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="advise"
                label="中标通知书"
                align="center"
                min-width="100">
                </el-table-column>
            </el-table>
        </div></el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            msg:[],
            formInline: {
                user: '',
                region: [{
                    value: '上海',
                    label: '上海'
                },{
                    value: '北京',
                    label: '北京'
                },{
                    value: '天津',
                    label: '天津'
                }],
                date1: '',
                date2:''
            },
            value:'上海',
            tableData: [{
                date: '1',
                name: '上海院',
                project: '轨道交通',
                work: '工程承包',
                item: '公开招标',
                province: '上海',
                win:'中铁',
                data1:'2016-05-03',
                data2:'2016-05-06',
                money:'20000',
                advise:'中标通知书'
                }, {
                date: '2',
                name: '十五局',
                project: '民用建筑',
                work: '工程承包',
                item: '私营企业',
                province: '上海',
                win:'中铁',
                data1:'2016-06-05',
                data2:'2016-07-03',
                money:'10020',
                advise:'中标通知书'
                }, {
                date: '3',
                name: '十四局',
                project: '隧道',
                work: '工程承包',
                item: '私营企业',
                province: '北京',
                win:'四十四',
                data1:'2017-06-03',
                data2:'2017-06-03',
                money:'20000',
                advise:'111'
                }]  
        }
    },
    mounted(){

    },
    methods: {
        search() {
            console.log('搜索');
        },
        handleClick(row) {
            this.$router.push({path:'/msg',query:{msgg:row}})
        },
        first({row,column,rowIndex,columnIndex}){
            // console.log(row,column,rowIndex,columnIndex)
            if((rowIndex===1||rowIndex===2||rowIndex===3||row.date)&&(columnIndex===0||columnIndex===1)){
                return "background:#E6E6E6"
            }
        }
    },
}
</script>
<style>
*{
    margin: 0;
    padding: 0
}
.item .admin{
    height: 30px;
    font-size: 14px;
    background: #9DD6F7;
}
.item .admin .adminer{
    margin-left: 10px;
    background: #fff;
    line-height: 30px;
    text-align: center;
    font-size: 12px;
}
.item .indexof{
    background: #EAF3FF;
    padding-bottom: 8px;
}
.item .indexof .el-col{
    margin-top: 8px;
}
.item .el-form-item{
    margin-bottom: 0 !important;
}
.item .el-form-item__label{
    font-size: 10px !important;
    margin-bottom: 0 !important;
}
.item .el-table td,.item .el-table th{
    padding:6px 0 !important;
}
.item .el-table--border {
  border: 1px solid #ccc !important;
}
.item .el-table--border td, .item .el-table th, .item .el-table tr{
  border: 1px solid #ccc !important;
  border-collapse: collapse;
}
.item .el-button--small,.item .el-button--small.is-round {
    padding:0 0;
}
</style>



