<template>
    <div class="busmsg">
        <el-tabs type="border-card">
            <el-tab-pane label="基本信息">
                <!-- 基本信息 -->
                <el-row class="info">
                    <el-col :span="24"><div class="grid-content bg-purple-dark">
                        <el-row>
                        <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                            <el-form ref="form" :model="form" label-width="80px" size="mini" :disabled="true">
                                <el-form-item label="省份:">
                                <el-select v-model="form.province" style="width:60%;">
                                    <el-option label="上海" value="上海"></el-option>
                                    <el-option label="北京" value="北京"></el-option>
                                    <el-option label="天津" value="天津"></el-option>
                                </el-select>
                                </el-form-item>
                            </el-form>
                        </div></el-col>
                        <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                            <el-form ref="form" :model="form" label-width="80px" size="mini" :disabled="true">
                                <el-form-item label="类别:">
                                <el-select v-model="form.keyitem" style="width:60%;" placeholder="请选择">
                                    <el-option label="年度" value="年度"></el-option>
                                    <el-option label="月度" value="月度"></el-option>
                                </el-select>
                                </el-form-item>
                            </el-form>
                        </div></el-col>
                        <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                            <el-form ref="form" :model="form" label-width="80px" size="mini" :disabled="true">
                                <el-form-item label="报告时间:">
                                <el-date-picker
                                value-format="yyyy-MM-dd"
                                format="yyyy-MM-dd"
                                v-model="form.reptime"
                                type='ny'
                                style="width:60%">
                                </el-date-picker>
                                </el-form-item>
                            </el-form>
                        </div></el-col>
                        <el-col :xs="24" :sm="24" :md="24"><div class="grid-content bg-purple-light">
                            <el-form ref="form" :model="form" label-width="80px" size="mini" :disabled="true">
                                <el-form-item label="备注:">
                                    <el-input type="textarea" v-model="form.remarks" style="width:80%"></el-input>
                                </el-form-item>
                            </el-form>
                        </div></el-col>
                        </el-row>
                    </div></el-col>
                </el-row>
            </el-tab-pane>
            <el-tab-pane label="上报附件">
                <!-- 上报附件 -->
                <el-row class="win">
                <el-col :span="24"><div class="grid-content tables bg-purple-dark">
                    上传附件
                </div></el-col>
                </el-row>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form:{}
        }
    },
    mounted(){
        this.form=this.$route.query.busmsg
        // console.log(this.form)
    },
    methods:{
        first({row,column,rowIndex,columnIndex}){
            // console.log(row,column,rowIndex,columnIndex)
            if((rowIndex===1||rowIndex===2||rowIndex===3||row.date)&&columnIndex===0){
                return "background:#E6E6E6"
            }
        },
        ny(val){
            console.log(val)
        }
    }
}
</script>
<style lang="scss">
.busmsg{
    .info{
        padding-top: 20px;
    }
    .el-tabs--border-card>.el-tabs__content{
        padding:0;
    }
    .el-form-item__label{
        font-size: 8px !important;
    }
    .el-table td,.el-table th{
        padding:6px 0 !important;
    }
    .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed{
        border-right: 1px solid #ccc !important;
    }
    .el-table td, .el-table th.is-leaf{
        border-bottom: 1px solid #ccc;
    }
}
</style>



