<template>
    <div class="partnermsg">
        <el-row>
            <el-col :span="24"><div class="grid-content bg-purple-dark">
                <el-row>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="单位名称:">
                            <el-input v-model="form.unitname" style="width:60%"></el-input>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="级别:">
                        <el-select v-model="form.level" style="width:60%;">
                            <el-option label="重要" value="重要"></el-option>
                            <el-option label="一般" value="一般"></el-option>
                            <el-option label="战略" value="战略"></el-option>
                        </el-select>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="注册资本金:">
                        <el-input v-model="form.capital" style="width:60%"></el-input>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="资产负债率:">
                        <el-input v-model="form.ratio" style="width:60%"></el-input>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="优势领域:">
                        <el-input v-model="form.areas" style="width:60%"></el-input>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="联系人:">
                        <el-input v-model="form.contacts" style="width:60%"></el-input>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="职务:">
                        <el-input v-model="form.post" style="width:60%"></el-input>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="联系电话:">
                        <el-input v-model="form.tel" style="width:60%"></el-input>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="forms" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="单位地址:">
                        <el-input v-model="forms.unitaddress" style="width:60%"></el-input>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="forms" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="座机:">
                        <el-input v-model="forms.landline" style="width:60%"></el-input>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                </el-row>
            </div></el-col>
        </el-row>
        <el-tabs type="border-card">
            <el-tab-pane label="合作项目">
                <!-- 合作项目 -->
                <el-row class="project">
                    <el-col :span="24"><div class="grid-content tables bg-purple-dark">
                        <el-table
                            :data="tableData"
                            border
                            style="width: 100%"
                            :header-cell-style="{background:'#E6E6E6',color:'#000'}">
                            <el-table-column
                            fixed
                            prop="date"
                            label="序号"
                            align="center"
                            width="50">
                            </el-table-column>
                            <el-table-column
                            prop="proname"
                            label="项目名称"
                            align="center"
                            width="90">
                            </el-table-column>
                            <el-table-column
                            prop="matter"
                            label="合作事项"
                            align="center"
                            width="90">
                            </el-table-column>
                            <el-table-column
                            prop="mattermoney"
                            label="合同额"
                            align="center"
                            width="90">
                            </el-table-column>
                            <el-table-column
                            prop="remarks"
                            label="备注"
                            align="center"
                            min-width="110">
                            </el-table-column>
                        </el-table>
                    </div></el-col>
                </el-row>
            </el-tab-pane>
            <el-tab-pane label="获奖情况">
                <!-- 获奖情况 -->
                <el-row class="win">
                <el-col :span="24"><div class="grid-content tables bg-purple-dark">
                    <el-table
                        :data="tableData1"
                        border
                        style="width: 100%"
                        :header-cell-style="{background:'#E6E6E6',color:'#000'}"
                        :cell-style="first">
                        <el-table-column
                        fixed
                        prop="date"
                        label=""
                        align="center"
                        width="50">
                        </el-table-column>
                        <el-table-column
                        prop="winname"
                        label="奖项名称"
                        width="90">
                        </el-table-column>
                        <el-table-column
                        prop="entryname"
                        label="项目名称"
                        width="90">
                        </el-table-column>
                        <el-table-column
                        prop="wintime"
                        label="获奖时间"
                        width="90">
                        </el-table-column>
                        <el-table-column
                        prop="remarks"
                        label="备注"
                        min-width="110">
                        </el-table-column>
                    </el-table>
                </div></el-col>
                </el-row>
            </el-tab-pane>
            <el-tab-pane label="营业执照、资质证书">
                <!-- 营业执照、资质证书 -->
                <el-row class="license">
                <el-col :span="24"><div class="grid-content tables bg-purple-dark">
                    <el-table
                        :data="tableData2"
                        border
                        style="width: 100%"
                        :header-cell-style="{background:'#E6E6E6',color:'#000'}"
                        :cell-style="first">
                        <el-table-column
                        fixed
                        prop="date"
                        label=""
                        align="center"
                        width="50">
                        </el-table-column>
                        <el-table-column
                        prop="filename"
                        label="文件名"
                        width="90">
                        </el-table-column>
                        <el-table-column
                        prop="filesize"
                        label="文件大小"
                        width="90">
                        </el-table-column>
                        <el-table-column
                        prop="uploadtime"
                        label="上传时间"
                        width="90">
                        </el-table-column>
                        <el-table-column
                        prop="remarks"
                        label="备注"
                        min-width="110">
                        </el-table-column>
                    </el-table>
                </div></el-col>
                </el-row>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form:{},
            forms:{
                unitaddress:'朝阳区',
                landline:'0310-029283'
            },
            tableData: [{
                date: '1',
                proname: 'A',
                matter: '轨道交通',
                mattermoney: '工程承包',
                remarks: '公开招标'
                }, {
                date: '2',
                proname: 'A',
                matter: '轨道交通',
                mattermoney: '工程承包',
                remarks: '公开招标'
                },{
                date: '3',
                proname: 'A',
                matter: '轨道交通',
                mattermoney: '工程承包',
                remarks: '公开招标'
            }],
            tableData1: [{
                date: '1',
                winname:'ee',
                entryname:'',
                wintime:'',
                remarks:''
                }, {
                date: '2',
                winname:'ss',
                entryname:'',
                wintime:'',
                remarks:''
                },{
                date: '3',
                winname:'ssa',
                entryname:'',
                wintime:'',
                remarks:''
            }],
            tableData2:[{
                date:'1',
                filename:'aaaa',
                filesize:'12kb',
                uploadtime:'',
                remarks:''
            },{
                date:'2',
                filename:'ccc',
                filesize:'2kb',
                uploadtime:'',
                remarks:''
            },{
                date:'3',
                filename:'bbb',
                filesize:'22kb',
                uploadtime:'',
                remarks:''
            }]
        }
    },
    mounted(){
        this.form=this.$route.query.partnermsg
        // console.log(this.form)
    },
    methods:{
        first({row,column,rowIndex,columnIndex}){
            // console.log(row,column,rowIndex,columnIndex)
            if((rowIndex===1||rowIndex===2||rowIndex===3||row.date)&&columnIndex===0){
                return "background:#E6E6E6"
            }
        }
    }
}
</script>
<style lang="scss">
.partnermsg{
    padding-top: 20px;
    .el-tabs--border-card>.el-tabs__content{
        padding:0;
    }
    .el-form-item__label{
        font-size: 8px !important;
    }
    .el-table td,.el-table th{
        padding:6px 0 !important;
    }
    .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed{
        border-right: 1px solid #ccc !important;
    }
    .el-table td, .el-table th.is-leaf{
        border-bottom: 1px solid #ccc;
    }
}
</style>



