<template>
    <div>
        <div class="title" style="text-align:center;margin-top:30px;">
            <span style="font-size:16px;">中标情况表</span>
            <select id="select">
                <option>年度</option>
                <option>季度</option>
                <option>月度</option>
            </select>
            <select id="select1">
                <option>2019</option>
                <option>2018</option>
                <option>2017</option>
            </select>
        </div>
        <div id="myChart4" :style="{width:'100%',height:'300px'}"></div>
    </div>
</template>
<script>
import $ from 'jquery'
export default {
    data(){
        return{
            
        }
    },
    mounted(){
        this.drawLine();
        $("#select").change(function(){
            $.ajax({
                type : "get",
                data : {
                    'vals':$('#select').val(),
                    'value':$('#select1').val()
                },
                dataType : "json",
                url : "",
                success : function(data) {
                    //将你的取得的data 放到Echarts里
                }
            });
            console.log(this.value,$('#select1').val())
        });
        $("#select1").change(function(){
            $.ajax({
                type : "get",
                data : {
                    'vals':$('#select').val(),
                    'value':$('#select1').val()
                },
                dataType : "json",
                url : "",
                success : function(data) {
                    //将你的取得的data 放到Echarts里
                }
            });
            console.log($('#select').val(),this.value)
        });
    },
    methods:{
        drawLine(){
            let myChart = this.$echarts.init(document.getElementById('myChart4'))
            myChart.setOption({
                tooltip: {
                    trigger: 'axis',
                    axisPointer : { // 坐标轴指示器，坐标轴触发有效
                        type : 'shadow' // 默认为直线，可选为：'line' | 'shadow'
                    }
                },
                grid:{
                    top:'20%',
                    bottom:'20%',
                    left:'13%',
                    right:'6%',
                    // height: '250',
                },
                legend: [
                    {
                        bottom: 0,
                        // right: "24%",
                        textStyle: {
                            color: "black",
                            fontSize: 12,
                            fontFamily: "微软雅黑"
                        },
                        itemWidth: 20,
                        itemHeight: 7,
                        data:[{
                            name: "总额（亿元）",
                            icon: "rect"
                        },{
                            name: "完成比例",
                            icon: "line"
                        }]
                    }
                ],
                xAxis: [
                        {
                            type: 'category',
                            data:["上海","浙江","福建","江西"],
                            axisTick:false,
                            axisLabel: {
				                interval:0,//横轴信息全部显示
				                //rotate:90,//-30度角倾斜显示    
                            }
                        }
                    ],
                    yAxis: [
                        {
                            axisLine: {show:false},
                            axisTick: {show:false},
                            // max:20,
                            type: 'value',
                            // name: '水量/ml',
                            min: 0,
                            max: 1000,
                            interval: 500,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        },
                        {
                            axisLine: {show:false},//y轴线
                            axisTick: {show:false},//y刻度线
                            // splitLine:{show:false},//不显示网格线
                            max:20,
                            type: 'value',
                            // name: '水量/ml',
                            min: 0,
                            max: 40,
                            interval: 20,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        }
                    ],
                series: [
                        {
                            name:'总额（亿元）',
                            type:'bar',
                            yAxisIndex:0,
                            barWidth: '30',
                            itemStyle: {
                                normal: {
                                    color: '#5B9BD5',
                                    /*信息显示方式*/
                                    // label: {
                                    //     show: true,
                                    //     position: 'top',
                                    //     // formatter: '{b}\n{c}'
                                    // }
                                }
                            },
                            data:['500','390','180','480']
                        },
                        {
                            name:'完成比例',
                            type:'line',
                            yAxisIndex:1,
                            showSymbol:false,//拐点不显示圆圈
                            symbol:'none',//悬停不显示圆点
                            itemStyle : {  /*设置折线颜色*/
                                normal : {
                                    color:'#ED833C'
                                }
                            },
                            data:['32','15','8','30']
                        }

                    ]
            });
            window.addEventListener("resize", () => { myChart.resize();});
        }
    }
}
</script>
<style scoped>
    #select{
        margin-left: 10px;
        outline: none;
        font-size: 12px;
    }
    #select1{
        outline: none;
        font-size: 12px;
    }
</style>



