<template>
    <div>
        <el-row>
        <el-col :xs="20" :sm="24" :md="24"><div class="grid-content bg-purple">
                <div class="block">
                    <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage4"
                    :page-sizes="[10, 15, 20, 25, 30]"
                    :page-size="pagesizes"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="104">
                    </el-pagination>
                </div>
        </div></el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data() {
      return {
        currentPage4: 1,
        pagesizes:25
      };
    },
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      }
    }
}
</script>
<style>
.page{
    background: red;
}
    /* .el-tabs__nav-scroll{
        overflow: scroll;
    } */
</style>



