<template>
    <div class="compemsg">
        <el-row>
            <el-col :span="24"><div class="grid-content bg-purple-dark">
                <el-row>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="省份:">
                        <el-select v-model="form.province" style="width:60%;">
                            <el-option label="上海" value="上海"></el-option>
                            <el-option label="天津" value="天津"></el-option>
                            <el-option label="北京" value="北京"></el-option>
                        </el-select>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="重点项目类别:">
                        <el-select v-model="form.keyitem" style="width:60%;">
                            <el-option label="A" value="A"></el-option>
                            <el-option label="B" value="B"></el-option>
                            <el-option label="C" value="C"></el-option>
                        </el-select>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                <el-col :xs="24" :sm="24" :md="24"><div class="grid-content bg-purple-light">
                    <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                        <el-form-item label="备注">
                            <el-input type="textarea" v-model="form.remarks" style="width:90%"></el-input>
                        </el-form-item>
                    </el-form>
                </div></el-col>
                </el-row>
            </div></el-col>
        </el-row>
        <el-tabs type="border-card">
            <el-tab-pane label="明细表">
                <!-- 明细表 -->
                <el-row class="achievement">
                    <el-col :span="24"><div class="grid-content tables bg-purple-dark">
                        <el-form :model="tableform" ref="tableForm" size="mini">
                        <el-table
                            :data="tableform.forms"
                            border
                            style="width: 100%"
                            :header-cell-style="{background:'#E6E6E6',color:'#000'}"
                            :cell-style="first">
                            <el-table-column
                            fixed
                            prop="date"
                            label=""
                            align="center"
                            width="50">
                            </el-table-column>
                            <el-table-column
                            prop="proname"
                            label="项目名称"
                            align="center"
                            width="90">
                            </el-table-column>
                            <el-table-column
                            prop="content"
                            label="建设内容"
                            align="center"
                            width="90">
                            </el-table-column>
                            <el-table-column
                            prop="quota"
                            label="投资额度"
                            align="center"
                            width="90">
                            </el-table-column>
                            <el-table-column
                            label="项目模式"
                            align="center"
                            width="130">
                                <template slot-scope="scope">
                                    <el-form-item :prop="`forms.${scope.$index}.models`"  >
                                    <!-- <el-input v-model="scope.row.models" :disabled="false"></el-input> -->
                                    <el-select v-model="scope.row.models" style="width:100%">
                                        <el-option label="公开招标" value="公开招标"></el-option>
                                        <el-option label="公开招标" value="公开招标"></el-option>
                                        <el-option label="公开招标" value="公开招标"></el-option>
                                    </el-select>
                                    </el-form-item>
                                </template>
                            </el-table-column>
                            <el-table-column
                            prop="consunit"
                            label="建设单位"
                            align="center"
                            width="90">
                            </el-table-column>
                            <el-table-column
                            prop="desunit"
                            label="设计单位"
                            align="center"
                            width="90">
                            </el-table-column>
                            <el-table-column
                            prop="ppt"
                            label="招标/ppt咨询单位"
                            align="center"
                            width="140">
                            </el-table-column>
                            <el-table-column
                            prop="coopunit"
                            label="牵头单位/协作单位/外部合作伙伴"
                            align="center"
                            width="230">
                            </el-table-column>
                            <el-table-column
                            label="项目所处阶段"
                            align="center"
                            width="110">
                                <template slot-scope="scope">
                                    <el-form-item :prop="`forms.${scope.$index}.stage`"  >
                                    <!-- <el-input v-model="scope.row.models" :disabled="false"></el-input> -->
                                    <el-select v-model="scope.row.stage" style="width:100%">
                                        <el-option label="1" value="1"></el-option>
                                        <el-option label="2" value="2"></el-option>
                                        <el-option label="3" value="3"></el-option>
                                    </el-select>
                                    </el-form-item>
                                </template>
                            </el-table-column>
                            <el-table-column
                            label="预计招标时间年月"
                            align="center"
                            width="150">
                                <template slot-scope="scope">
                                    <el-form-item :prop="`forms.${scope.$index}.tender`"  >
                                        <el-date-picker type="date" placeholder="选择日期" v-model="scope.row.tender" style="width: 100%;"></el-date-picker>
                                    </el-form-item>
                                </template>
                            </el-table-column>
                            <el-table-column
                            prop="progress"
                            label="项目进展及推进计划"
                            align="center"
                            width="150">
                            </el-table-column>
                            <el-table-column
                            prop="remarks"
                            label="备注"
                            align="center"
                            min-width="100">
                            </el-table-column>
                        </el-table>
                        </el-form>
                    </div></el-col>
                </el-row>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form:{},
            tableform:{
                forms:[{
                    date: '1',
                    state:'',
                    keyitem:'A',
                    province:'北京',
                    proname:'',
                    content:'',
                    quota:'',
                    models: '公开招标',
                    consunit:'',
                    desunit:'',
                    ppt: '轨道交通',
                    coopunit: '工程承包',
                    stage:'1',
                    tender: '',
                    progress:'',
                    remarks:''
                    },{
                    date: '2',
                    state:'',
                    keyitem:'B',
                    province:'北京',
                    proname:'',
                    content:'',
                    quota:'',
                    models: '公开招标',
                    consunit:'',
                    desunit:'',
                    ppt: '轨道交通',
                    coopunit: '工程承包',
                    stage:'2',
                    tender: '',
                    progress:'',
                    remarks:''
                    },{
                    date: '3',
                    state:'',
                    keyitem:'C',
                    province:'北京',
                    proname:'',
                    content:'',
                    quota:'',
                    models: '公开招标',
                    consunit:'',
                    desunit:'',
                    ppt: '轨道交通',
                    coopunit: '工程承包',
                    stage:'3',
                    tender: '',
                    progress:'',
                    remarks:''
                }]
            }
        }
    },
    mounted(){
        this.form=this.$route.query.keypromsg
    },
    methods:{
        first({row,column,rowIndex,columnIndex}){
            // console.log(row,column,rowIndex,columnIndex)
            if((rowIndex===1||rowIndex===2||rowIndex===3||row.date)&&columnIndex===0){
                return "background:#E6E6E6"
            }
        }
    }
}
</script>
<style lang="scss">
.compemsg{
    padding-top: 20px;
    .el-tabs--border-card>.el-tabs__content{
        padding:0;
    }
    .el-form-item__label{
        font-size: 8px !important;
    }
    .el-table td,.el-table th{
        padding:6px 0 !important;
    }
    .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed{
        border-right: 1px solid #ccc !important;
    }
    .el-table td, .el-table th.is-leaf{
        border-bottom: 1px solid #ccc;
    }
    .tables{
        .el-form-item--mini.el-form-item, .el-form-item--small.el-form-item{
            margin-bottom: 0;
        }
        .el-form-item{
            margin-bottom: 0;
        }
    }
}
</style>



