<template>
    <div style="text-align:center;">
        <!-- <div class="title">在建项目分布</div> -->
        <div id="mapChart" style="width:100%;height:300px;margin-top:30px;"></div>
    </div>
</template>
<script>
import echarts from "echarts"
import "../../../node_modules/echarts/map/js/china.js";
export default {
  mounted() {
    // this.init();
      let myChart = echarts.init(document.getElementById('mapChart'));
      var datas=[
              {name: '福建', value: '项目名称一',selected:true},
              {name: '上海', value: '项目名称二',selected:true},
              {name: '浙江', value: '项目名称三',selected:true},
              {name: '江西', value: '项目名称四',selected:true}
          ]
      let option = {
        title:{
          text:'在建项目分布',
          left:'left',
          textStyle:{
            fontSize:14
          }
        },
        tooltip: {
            trigger: 'item',
            formatter(params){
              var info = params.name;
              var res = '';
              for(var i = 0;i<datas.length;i++){
                    if(datas[i].name == info){
                        res = `<div class="select"><p>`+info+`</p><p>`+datas[i].value+`</p></div>`;
                        break;
                    }
                }
              return res; 
            },
            backgroundColor:"#fff",//提示标签背景颜色
            textStyle:{color:"#000"} //提示标签字体颜色
        },
        series:[{
          type: 'map',
          mapType: 'china', // 表示中国地图
          mapLocation: {
            x: 'center'
          },
          //selectedMode : 'multiple',//选中效果
          zoom: 1.2, // 当前视角的缩放比例（地图的放大比例）
          label: {
            normal:{
              show:false
            },
            emphasis:{
              show:false
            }
          },
          itemStyle: { // 地图区域的多边形 图形样式。
            normal:{
              borderColor: 'rgb(169, 223, 248)',
              borderWidth:2,
              areaColor: "#83C9FD",
            },
            emphasis: {                          //高亮状态
              areaColor: 'orange',  //高亮时候地图显示的颜色
              borderWidth:0     //高亮时候的边框宽度
            },
          },
          data:datas
        }]
      };
      myChart.setOption(option);
      window.addEventListener("resize", () => { myChart.resize();});
    },
};
</script>
<style lang="scss">
  .select{
    border: 1px solid #ccc;
    box-shadow: 2px 2px 4px 2px #ccc;
  }
</style>



