<template>
    <div class="time">
        <el-row class="time-header">
            <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="省份">
                        <el-select v-model="form.region" placeholder="-请选择-" style="width:70%">
                        <el-option label="上海" value="上海"></el-option>
                        <el-option label="北京" value="北京"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
        <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple">
            <el-form ref="form" :model="form" label-width="80px" size="mini">
                <el-form-item label="开始时间">
                    <el-time-select
                    v-model="form.value1"
                    placeholder="选择时间"
                    style="width:70%">
                    </el-time-select>
                </el-form-item>
            </el-form>
        </div></el-col>
        <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple-light">
            <el-form ref="form" :model="form" label-width="80px" size="mini">
                <el-form-item label="结束时间">
                    <el-time-select
                    v-model="form.value2"
                    placeholder="选择时间"
                    style="width:70%">
                    </el-time-select>
                </el-form-item>
            </el-form>
        </div></el-col>
        <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple-light">
            <el-form label-width="80px" size="mini">
                <el-form-item>
                    <el-button style="background:#F2F2F2;border:1px solid #CCCFD2;outline:none" @click="search">搜索</el-button>
                </el-form-item>
            </el-form>
        </div></el-col>
        </el-row>
        <el-row>
        <el-col :span="24"><div class="grid-content time-title bg-purple-dark">
            华东区域项目中标统计表
        </div></el-col>
        </el-row>
         <el-row>
        <el-col :span="24"><div class="grid-content table bg-purple-dark">
            <el-table
            :data="tableData"
            border
            style="width: 100%"
            :header-cell-style="{color:'#000'}"
            :row-style="tableRowStyle">
            <el-table-column
            fixed
            prop="data"
            label="序号"
            width="50"
            align="center">
            </el-table-column>
            <el-table-column
            prop="name"
            label="中标单位名称"
            width="110"
            align="center">
            </el-table-column>
            <el-table-column
            prop="itemname"
            label="项目名称"
            width="110"
            align="center">
            </el-table-column>
            <el-table-column
            prop="basic"
            label="项目概况"
            width="140"
            align="center">
            </el-table-column>
            <el-table-column
            prop="city"
            label="工程地点"
            width="90"
            align="center">
            </el-table-column>
            <el-table-column
            prop="items"
            label="工程类别"
            width="90"
            align="center">
            </el-table-column>
            <el-table-column
            prop="money"
            label="合同金额(万元)"
            width="120"
            align="center">
            </el-table-column>
            <el-table-column
            prop="hostname"
            label="业主单位名称"
            width="110"
            align="center">
            </el-table-column>
            <el-table-column
            prop="date1"
            label="中标日期"
            width="110"
            align="center">
            </el-table-column>
            <el-table-column
            prop="date2"
            label="合同签署日期"
            width="110"
            align="center">
            </el-table-column>
            <el-table-column
            prop="common"
            label="备注"
            min-width="100"
            align="center">
            </el-table-column>
        </el-table>
        </div></el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form:{
                value1:'',
                value2:''
            },
            tableData: [{
                data:'1',
                name: '11局',
                itemname: '上海轨道交通',
                basic: '此模块主要实现',
                city: '上海',
                items: '轨道交通',
                money:'10000',
                hostname:'中铁项目公司',
                date1:'2018年3月4日',
                date2:'2017年3月4日',
                common:''
                }]
        }
    },
    methods:{
        search() {
            console.log('搜索');
        },
        tableRowStyle({ row, rowIndex }) {
            return 'color: #FF6E6E'
        },
    }
}
</script>
<style lang="scss">
.time .time-header{
    background: #EAF3FF;
    padding-bottom: 8px;
}
.time .time-header .el-form-item__label{
    font-size: 10px !important;
    margin-bottom: 0 !important;
}
.time .el-col{
    margin-top: 8px;
}
.time .time-header .el-form-item{
    margin-bottom: 0 !important;
}
.time .time-title{
    text-align: center;
    line-height: 36px;
    font-size: 16px;
}
.time .table th, .table td{
    vertical-align: middle !important;
    padding: 6px 0 !important;
    border-top: none;
}
.time{
    .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed{
        // border-right: 1px solid #000;
        border: 1px solid #000;
    }
    .el-table--border{
        border-bottom: 1px solid #000;
    }
    .el-table td, .el-table th.is-leaf{
        border-bottom: 1px solid #000;
    }
}
</style>



