<template>
    <div class="date">
        <el-tabs type="border-card">
        <el-tab-pane label="项目信息">
            <el-row>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="项目名称:">
                        <el-input v-model="form.name" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="工程类别:">
                    <el-input v-model="form.project" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="业务板块:">
                    <el-input v-model="form.work" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="项目模式:">
                    <el-input v-model="form.item" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="formdata" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="工程地点:">
                    <el-input v-model="formdata.address" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="合同金额(万元):">
                    <el-input v-model="form.money" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="中标日期:">
                    <el-input v-model="form.data1" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="formdata" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="业主单位名称:">
                    <el-input v-model="formdata.hostname" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="施工单位:">
                    <el-input v-model="form.unit" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="项目经理:">
                    <el-input v-model="form.manager" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="联系电话:">
                    <el-input v-model="form.tel" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="项目状态:">
                        <el-select v-model="form.state" style="width:70%;">
                            <el-option label="未建" value="未建"></el-option>
                            <el-option label="在建" value="在建"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="formdata" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="合同开工时间:">
                    <el-input v-model="formdata.begin" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="formdata" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="合同竣工时间:">
                    <el-input v-model="formdata.end" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="省份:">
                        <el-select v-model="form.province" style="width:70%;">
                            <el-option label="上海" value="上海"></el-option>
                            <el-option label="北京" value="北京"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="formdata" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="实际开工日期">
                        <el-date-picker type="date" v-model="form.realbegin" style="width: 70%;"></el-date-picker>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="formdata" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="实际竣工日期">
                        <el-date-picker type="date" v-model="form.realend" style="width: 70%;"></el-date-picker>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="formdata" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="重点项目类别:">
                    <el-input v-model="formdata.keyitem" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="24" :md="24"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="formdata" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="项目概况">
                        <el-input type="textarea" v-model="formdata.desc" style="width:90%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="24" :md="24"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="formdata" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="备注">
                        <el-input type="textarea" v-model="formdata.common" style="width:90%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            </el-row>
        </el-tab-pane>
        <el-tab-pane label="获奖信息">
            {{form.win}}
        </el-tab-pane>
        <el-tab-pane label="处罚信息">{{form.punish}}</el-tab-pane>
        <el-tab-pane label="合同附件">合同附件</el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form:{},
            formdata:{
                address:'上海',
                hostname:'qq',
                begin:'2015-11-23',
                end:'2016-11-23',
                realbegin:'',
                realend:'',
                keyitem:'1',
                desc:'',
                common:''
            }
        }
    },
    mounted(){
        this.form=this.$route.query.date
        // console.log(this.form)
    }
}
</script>
<style>
.date .el-form-item__label{
    font-size: 8px !important;
}
</style>



