<template>
    <div>
        <!-- <div class="title" style="text-align:center;">区域总部重点项目</div> -->
        <div id="myChart5" :style="{width:'100%',height:'300px',marginTop:'30px'}"></div>
    </div>
</template>
<script>
export default {
    data(){
        return {

        }
    },
    mounted(){
        this.drawLine();
        // this.init()
    },
    methods:{
        drawLine(){
            let myChart = this.$echarts.init(document.getElementById('myChart5'))
            myChart.setOption({
                title:{
                    text:'区域总部重点项目',
                    left:'center',
                    textStyle:{
                        fontSize:16,
                        fontWeight:'normal'
                    }
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer : { // 坐标轴指示器，坐标轴触发有效
                        type : 'shadow' // 默认为直线，可选为：'line' | 'shadow'
                    }
                },
                grid:{
                    top:'20%',
                    bottom:'20%',
                    left:'6%',
                    right:'10%',
                    // height: '250',
                },
                legend: [
                    {
                        bottom: 0,
                        // right: "24%",
                        textStyle: {
                            color: "black",
                            fontSize: 12,
                            fontFamily: "微软雅黑"
                        },
                        itemWidth: 20,
                        itemHeight: 7,
                        data:[{
                            name: "项目数量",
                            icon: "rect"
                        },{
                            name: "合同额（亿元）",
                            icon: "line"
                        }]
                    }
                ],
                xAxis: [
                        {
                            type: 'category',
                            data:["上海","浙江","江西","福建"],
                            axisTick:false,
                            axisLabel: {
				                interval:0,//横轴信息全部显示
				                rotate:0,//-30度角倾斜显示    
                            }
                        }
                    ],
                    yAxis: [
                        {
                            axisLine: {show:false},
                            axisTick: {show:false},
                            // max:20,
                            type: 'value',
                            // name: '水量/ml',
                            min: 0,
                            max: 6,
                            interval: 2,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        },
                        {
                            axisLine: {show:false},//y轴线
                            axisTick: {show:false},//y刻度线
                            max:20,
                            type: 'value',
                            // name: '水量/ml',
                            min: 0,
                            max: 15,
                            interval: 5,
                            axisLabel: {
                                formatter: '{value} '
                            }
                        }
                    ],
                series: [
                        {
                            name:'项目数量',
                            type:'bar',
                            yAxisIndex:0,
                            barWidth: '20',
                            itemStyle: {
                                normal: {
                                    color: '#5B9BD5',
                                    /*信息显示方式*/
                                    // label: {
                                    //     show: true,
                                    //     position: 'top',
                                    //     // formatter: '{b}\n{c}'
                                    // }
                                }
                            },
                            data:["5","3","4","2"]
                        },
                        {
                            name:'合同额（亿元）',
                            type:'line',
                            yAxisIndex:1,
                            showSymbol:false,//拐点不显示圆圈
                            symbol:'none',//悬停不显示圆点
                            itemStyle : {  /*设置折线颜色*/
                                normal : {
                                    color:'#ED833C'
                                }
                            },
                            data:["10","4","7","3"]
                        }

                    ]
            });
            window.addEventListener("resize", () => { myChart.resize();});
        }
    }
}
</script>
<style lang="scss" scoped>
    #myChart5{
        width: 100%;
    }
</style>
 



