<template>
    <div class="custom">
        <el-row>
        <el-col :span="24" class="zai-title"><div class="grid-content bg-purple-dark">
            <el-col :span="8"><div class="grid-content zai-title-title bg-purple">核心客户</div></el-col>
        </div></el-col>
        </el-row>
        <el-row class="indexof">
            <el-col :xs="24" :sm="12" :md="6"><div class="grid-content bg-purple-light">
                <el-form :model="form" label-width="80px" size="mini">
                    <el-form-item label="姓名">
                        <el-input v-model="form.user" style="width:60%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="4"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="类别">
                        <el-select v-model="form.category" style="width:60%">
                        <el-option 
                        v-for="i in form.categores"
                        :key="i.value"
                        :label="i.label"
                        :value="i.value"
                        ></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="10"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="80px" size="mini">
                    <el-form-item label="维护人">
                        <el-input v-model="form.defender" style="width:60%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="4"><div class="grid-content bg-purple-light">
                <el-form label-width="80px" size="mini">
                    <el-form-item>
                        <el-button style="background:#F2F2F2;border:1px solid #CCCFD2;outline:none" @click="search">搜索</el-button>
                    </el-form-item>
                </el-form>
            </div></el-col>
        </el-row>
        <el-row>
        <el-col :span="24"><div class="grid-content tables bg-purple-dark">
            <el-table
                :data="tableData"
                border
                style="width: 100%"
                :header-cell-style="{background:'#E6E6E6',color:'#000'}">
                <el-table-column
                fixed
                label="操作"
                align="center"
                width="50">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small" style="outline:none;">查看</el-button>
                </template>
                </el-table-column>
                <el-table-column
                fixed
                prop="date"
                label="序号"
                align="center"
                width="50">
                </el-table-column>
                <el-table-column
                prop="category"
                label="类别"
                align="center"
                width="60">
                </el-table-column>
                <el-table-column
                prop="unit"
                label="单位"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="branch"
                label="部门"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="userpost"
                label="姓名，职务"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="phone"
                label="手机"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="trackname"
                label="跟踪项目名称"
                align="center"
                width="120">
                </el-table-column>
                <el-table-column
                prop="undername"
                label="在建项目名称"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="build"
                label="承建单位"
                align="center"
                width="80">
                </el-table-column>
                <el-table-column
                prop="defender"
                label="维护人"
                align="center"
                width="90">
                </el-table-column>
                <el-table-column
                prop="visit"
                label="拜访频率"
                align="center"
                width="110">
                </el-table-column>
                <el-table-column
                prop="remarks"
                label="备注"
                align="center"
                min-width="100">
                </el-table-column>
            </el-table>
        </div></el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form:{
                user:'',
                category:'A',
                categores:[{
                    value:'A',
                    label:'A'
                },{
                    value:'B',
                    label:'B'
                },{
                    value:'C',
                    label:'C'
                }],
                defender:'',
            },
            tableData: [{
                date: '1',
                category: 'A',
                unit: '轨道交通',
                branch: '工程承包',
                userpost: '公开招标',
                phone: '13365725692',
                trackname:'2016-05-03',
                undername:'20000',
                build:'',
                defender:'',
                visit:'每周',
                remarks:'在建'
                }, {
                date: '1',
                category: 'B',
                unit: '轨道交通',
                branch: '工程承包',
                userpost: '公开招标',
                phone: '110',
                trackname:'2016-05-03',
                undername:'20000',
                build:'',
                defender:'',
                visit:'每月',
                remarks:'在建'
                },{
                date: '1',
                category: 'C',
                unit: '轨道交通',
                branch: '工程承包',
                userpost: '公开招标',
                phone: '18134214803',
                trackname:'2016-05-03',
                undername:'20000',
                build:'',
                defender:'',
                visit:'一年',
                remarks:'无'
                }]  
        }
    },
    methods:{
        search(){
            console.log(1)
        },
        handleClick(row) {
            this.$router.push({path:'/custommsg',query:{custommsg:row}})
        }
    }
}
</script>
<style lang="scss">
.custom{
    .zai-title{
        background: #9AD5F8;
        .zai-title-title{
            background: #fff;
            margin-left: 5px;
            text-align: center;
            font-size: 12px;
            line-height: 26px;
        }
    }
    .indexof{
        background: #E6F3FF;
        padding-bottom: 8px;
        .el-form-item{
            margin-bottom: 0 !important;
        }
        .el-form-item__label{
            font-size: 10px !important;
            margin-bottom: 0 !important;
        }
        .el-col{
            margin-top: 8px;
        }
    }
    .tables{
        .el-table td,.el-table th{
            padding:6px 0 !important;
        }
        .el-table--border td, .el-table--border th, .el-table__body-wrapper .el-table--border.is-scrolling-left~.el-table__fixed{
            border-right: 1px solid #ccc !important;
        }
        .el-table td, .el-table th.is-leaf{
            border-bottom: 1px solid #ccc;
        }
        .el-button--small, .el-button--small.is-round {
            padding:0 0;
        }
    }
}
</style>



