<template>
    <div>
        <!-- <div class="title" style="text-align:center">集团重点项目</div> -->
        <div id="myChart3" :style="{width:'100%',height:'300px',marginTop:'30px'}"></div>
    </div>
</template>
<script>
export default {
    data(){
        return{

        }
    },
    mounted(){
        this.drawLine()
    },
    methods:{
        drawLine(){
            let myChart = this.$echarts.init(document.getElementById('myChart3'))
             myChart.setOption({
                 title:{
                     text:'集团重点项目',
                     left:'center',
                     textStyle:{
                        fontSize:16,
                        fontWeight:'normal'
                    }
                 },
                 tooltip : {
                    trigger: 'axis',
                    axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                        type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                    }
                },
                legend: [
                {
                    bottom: -3,
                    // right: "24%",
                    textStyle: {
                        color: "black",
                        fontSize: 12,
                        fontFamily: "微软雅黑"
                    },
                    itemWidth: 10,
                    itemHeight: 10,
                    data:[
                    {
                        name: "合同额（亿元）",
                        icon: "rect"
                    },{
                        name: "项目数量",
                        icon: "rect"
                    }]
                }
                ],
                grid: {
                    left: '6%',
                    right: '6%',
                    bottom: '30',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    data: ['11局','12局','大桥局','14局','15局','16局','17局','17局','18局','19局','20局','21局','22局','23局','24局','25局','中铁建设','中铁城建','物资集团','电气化局','投资集团','地产集团','铁四院','铁一院','铁五院','上海院'],
                    axisTick:false,
                    axisLabel: {
				        interval:0,//横轴信息全部显示
                        rotate:90,//-30度角倾斜显示    
                        textStyle:{
                            fontSize:10
                        },
                        formatter : function (value) {
                            let valueTxt = '';
                            if (value.length > 3) {
                                valueTxt = value.substring(0, 3) + '...';
                            }else {
                                valueTxt = value;
                            }
                            return valueTxt ;
                        }
                    }
                },
                yAxis:  [
                    {
                        type: 'value',
                        axisLine: {show:false},
                        axisTick: {show:false},
                        min: 0,
                        max: 30,
                        interval: 10
                    },
                    {
                        type: 'value',
                        axisLine: {show:false},
                        axisTick: {show:false},
                        splitLine:{show:false},
                        min: 0,
                        max: 10,
                        interval: 5,
                    }
                ],
                series: [
                    {
                        name: '合同额（亿元）',
                        type: 'bar',
                        stack: '总量',
                        barWidth:6,
                        label: {
                            normal: {
                                // show: true
                            }
                        },
                        itemStyle : {  /*设置柱状颜色*/
                            normal : {
                                color:'#4472C4'
                            }
                        },
                        data: [10, 7, 3, 3, 2, 5, 0,5,0,7,0,13,2,5,0,6,7,2,5,15,15,20,17,13,10,10]
                    },
                    {
                        name: '项目数量',
                        type: 'bar',
                        stack: '总量',
                        yAxisIndex:1,
                        barWidth:6,
                        label: {
                            normal: {
                                // show: true
                            }
                        },
                        itemStyle : {  /*设置柱状颜色*/
                            normal : {
                                color:'#ED7D31'
                            }
                        },
                        data: [3, 2, 4, 1, 3, 7, 0,4,0,3,0,0,0,4,0,0.2,0,0,0.1,0,0,2,0,0]
                    }
                ]
             });
             window.addEventListener("resize", () => { myChart.resize();});
        }
    }
}
</script>
<style scoped>

</style>



