<template>
    <div>
        <!-- <div class="title" style="text-align:center">区域营业收入完成情况</div> -->
        <div id="myChart2" :style="{width:'100%',height:'300px',marginTop:'30px'}"></div>
    </div>
</template>
<script>
import $ from 'jquery'
export default {
    data(){
        return {
        //    as:this.areass
        }
    },
    // props:['areass'],
    mounted(){
        this.drawLine();
        // console.log(this.months)
    },
    methods:{
        drawLine(){
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById('myChart2'))
            // 绘制图表
            myChart.setOption({
                title:{
                    text:'区域营业收入完成情况',
                    left:'center',
                    textStyle:{
                        fontSize:16,
                        fontWeight:'normal'
                    }
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross',
                        crossStyle: {
                            color: '#999'
                        }
                    }
                },
                 grid:{
                    top:'20%',
                    bottom:'20%',
                    left:'13%',
                    right:'13%',
                    // height: '250',
                },
                legend: [
                    {
                        bottom: 0,
                        // right: "24%",
                        textStyle: {
                            color: "black",
                            fontSize: 12,
                            fontFamily: "微软雅黑"
                        },
                        itemWidth: 20,
                        itemHeight: 7,
                        data:[
                        {
                            name: "计划（亿元）",
                            icon: "rect"
                        },{
                            name: "完成（亿元）",
                            icon: "rect"
                        },{
                            name: "比例",
                            icon: "line"
                        }]
                    }
                ],
                xAxis: [
                    {
                        axisTick:false,
                        type: 'category',
                        data: ['上海','浙江','福建','江西'],
                        axisPointer: {
                            type: 'shadow'
                        },
                        axisLine: {
                            lineStyle: {
                                color: '#ccc',//左边线的颜色
                            }
                        },
                        axisLabel: {
                            textStyle: {
                                fontSize:12,
                                color:'#000',
                            }
                        }
                    }
                ],
                yAxis: [
                    {
                        type: 'value',
                        // name: '水量',
                        axisTick:false,
                        axisLine: {show:false},
                        min: 0,
                        max: 1000,
                        interval: 500,
                        axisLabel: {
                            // formatter: '{value} ml'
                        }
                    },
                    {
                        type: 'value',
                        // name: '温度',
                        axisTick:false,
                        axisLine: {show:false},
                        splitLine:{show:false},//不显示网格线
                        min: 0,
                        max: 100,
                        interval: 50,
                        axisLabel: {
                            // formatter: '{value} °C'
                        }
                    }
                ],
                series: [
                    {
                        name:'计划（亿元）',
                        type:'bar',
                        barWidth: '20',
                        yAxisIndex:0,
                        itemStyle : {  /*设置柱状颜色*/
                            normal : {
                                color:'#70AD47'
                            }
                        },
                        data:[500, 395, 180, 420]
                    },
                    {
                        name:'完成（亿元）',
                        type:'bar',
                        barWidth: '20',
                        yAxisIndex:0,
                        itemStyle : {  /*设置柱状颜色*/
                            normal : {
                                color:'#4472C4'
                            }
                        },
                        data:[323, 313, 150, 380]
                    },
                    {
                        name:'比例',
                        type:'line',
                        yAxisIndex:1,
                        showSymbol:false,//拐点不显示圆圈
                        symbol:'none',//悬停不显示圆点
                        itemStyle : {  /*设置折线颜色*/
                            normal : {
                                color:'#ED833C'
                            }
                        },
                        // yAxisIndex: 1,
                        data:[60, 80, 30, 70]
                    }
                ]
            });
            window.addEventListener("resize", () => { myChart.resize();});
        }
    }
}
</script>
<style scoped>

</style>



