<template>
    <div class="textarea">
        <el-row>
        <el-col :span="24"><div class="grid-content bg-purple-dark">
            <el-tabs type="border-card">
            <el-tab-pane label="基本信息">
            <el-row>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="项目名称:">
                        <el-input v-model="form.name" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="工程类别:">
                    <el-select v-model="form.value" style="width:70%">
                    <el-option
                    v-for="item in form.project"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                    </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="业务板块:">
                        <el-select v-model="form.value1" style="width:70%">
                        <el-option
                        v-for="item in form.work"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="项目模式:">
                        <el-select v-model="form.index" style="width:70%">
                        <el-option
                        v-for="item in form.items"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="工程地点:">
                        <el-input v-model="form.place" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="省份:">
                        <el-select v-model="form.city" style="width:70%">
                        <el-option
                        v-for="item in form.province"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="业主单位名称:">
                        <el-input v-model="form.owner" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="中标日期">
                        <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 70%;"></el-date-picker>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="中标单位名称:">
                        <el-input v-model="form.win" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="合同金额(万元):">
                        <el-input v-model="form.money" style="width:70%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="合同签署日期">
                        <el-date-picker type="date" placeholder="选择日期" v-model="form.date2" style="width: 70%;"></el-date-picker>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="12" :md="12"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="重点项目类别:">
                        <el-select v-model="form.classes" style="width:70%;">
                            <el-option
                            v-for="item in form.class"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="24" :md="24"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="项目概况">
                        <el-input type="textarea" v-model="form.desc" style="width:90%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            <el-col :xs="24" :sm="24" :md="24"><div class="grid-content bg-purple-light">
                <el-form ref="form" :model="form" label-width="100px" size="mini" :disabled="true">
                    <el-form-item label="备注">
                        <el-input type="textarea" v-model="form.common" style="width:90%"></el-input>
                    </el-form-item>
                </el-form>
            </div></el-col>
            </el-row>
            </el-tab-pane>
            <el-tab-pane label="中标通知书">{{form.advise}}</el-tab-pane>
            </el-tabs>
        </div></el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data(){
        return{
            form: {
                name: '',
                project: [{
                    value: '轨道交通',
                    label: '轨道交通'
                },{
                    value: '隧道',
                    label: '隧道'
                },{
                    value: '民用建筑',
                    label: '民用建筑'
                }],
                value:'',
                work:[{
                    value:'工程承包',
                    label:'工程承包'
                },{
                    value:'编辑',
                    label:'编辑'
                }],
                value1:'',
                items:[{
                    value:'公开招标',
                    label:'公开招标'
                },{
                    value:'私营企业',
                    label:'私营企业'
                }],
                index:'',
                place:'',
                province:[{
                    value:'上海',
                    label:'上海'
                },{
                    value:'北京',
                    label:'北京'
                }],
                city:'',
                owner:'',
                date1: '',
                win:'',
                money:'',
                date2:'',
                class:[{
                    value:'股份重点',
                    label:'股份重点'
                },{
                    value:'扩建项目',
                    label:'扩建项目'
                }],
                classes:'股份重点',
                desc: '',
                common:'',
                advise:''
            }
        }
    },
    mounted(){
        this.form.name=this.$route.query.msgg.name
        this.form.value=this.$route.query.msgg.project
        this.form.value1=this.$route.query.msgg.work
        this.form.city=this.$route.query.msgg.province
        this.form.index=this.$route.query.msgg.item
        this.form.date1=this.$route.query.msgg.data1
        this.form.date2=this.$route.query.msgg.data2
        this.form.win=this.$route.query.msgg.win
        this.form.money=Number(this.$route.query.msgg.money/10000).toFixed(2)
        this.form.advise=this.$route.query.msgg.advise
    },
    methods: {
      
    }
}
</script>
<style>
.textarea .el-form-item__label{
    font-size: 8px !important;
}
</style>



